.html-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 200;
  touch-action: none;
  user-select: none;
  // pointer-events: none;
}

.html-overlay__logo {
  position: absolute;
  top: 35px;
  left: 35px;
  width: 80px;
}

.html-overlay__logo--right {
  left: auto;
  right: 35px;
  width: 95px;
}

.html-overlay__discord--right {
  top: auto;
  right: 0;
  left: 0;
  margin: auto;
  text-align: center;
  bottom: 30px;
  //width:500px;
  //height:50px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 150px;
    transform: translateY(0px);
    transition: transform 150ms ease-in;
    pointer-events: auto;

    &:hover {
      transform: translateY(-4px);
      transition: transform 250ms ease-out;
    }
  }
}

.html-overlay__mouse-infos {
  position: absolute;
  bottom: 35px;
  right: 35px;
  width: 100px;
  text-align: right;
  font-size: 10px;
  font-family: "Source Code Pro", sans-serif;
}

.html-overlay__console {
  position: absolute;
  bottom: 35px;
  left: 35px;
  width: 250px;
  height: 64px;
  overflow: hidden;
  font-size: 10px;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  font-family: "Source Code Pro", sans-serif;
}

@media (max-width: 767px) {
  .html-overlay__mouse-infos,
  .html-overlay__console {
    display: none;
  }
}

.html-overlay__logo__link {
  margin: 0;
  padding: 0;
  display: block;
  pointer-events: auto;
}

.html-overlay__logo__link__image {
  width: 100%;
}
