.ui-container {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 999;
  // pointer-events: none;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ui-container--hidden {
  display: none;
}

.ui-container__center {
  position: relative;
  flex-direction: column;
  pointer-events: none;
  user-select: none;
  svg {
    margin: 10px;
    transform: translateY(50px);
  }
}

.ui-container__center__searching {
  position: absolute;
  bottom: -80px;
  left: 0;
}

.ui-top-left {
  position: absolute;
  top: 40px;
  left: 40px;
  pointer-events: none;
  user-select: none;
}

.ui-top-right {
  position: absolute;
  top: 40px;
  right: 40px;
}

.ui-top-right__scan {
  position: absolute;
  top: 15px;
  right: 160px;
}

.ui-top-right__winner {
  position: absolute;
  top: 145px;
  right: 20px;
  font-size: 10px;
  text-align: right;
}

.ui-bottom-left {
  position: absolute;
  bottom: 40px;
  left: 40px;
}

.ui-bottom-right {
  position: absolute;
  bottom: 40px;
  right: 40px;
  display: flex;
}

.ui-top-right__current-step {
  position: absolute;
  top: 230px;
  right: 31px;
  font-size: 3rem;
  width: 70px;
  text-align: center;
}

.ui-top-right__current-step-next {
  position: absolute;
  top: 254px - 20px;
  right: 16px - 20px;
  padding: 20px;
}

.ui-top-right__current-step-prev {
  position: absolute;
  top: 255px - 20px;
  right: 108px - 20px;
  padding: 20px;
  svg {
    transform: rotate(180deg);
  }
}

.ui-top-right__current-step-next,
.ui-top-right__current-step-prev {
  opacity: 0.3;
  transition: all 200ms ease;
  &--active {
    cursor: pointer;
    opacity: 1;
  }
}

.ui-top-right__glyph {
  position: absolute;
  width: 150px;
  height: 150px;
  top: 17px;
  right: 10px;
}

.ui-top-right__unknown-glyph {
  position: absolute;
  width: 150px;
  height: 150px;
  top: 17px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3em;
}

.ui-center-left {
  position: absolute;
  width: 375px;
  bottom: 250px;
  left: 50px;
}

.ui-top-right__quest-current-step {
  position: absolute;
  width: 150px;
  /* height: 80px; */
  top: 420px;
  right: 5px;
  opacity: 0.74;
  font-family: RobotoMono-Regular;
  font-size: 12px;
  color: #dedede;
  text-align: right;
}
